                else {
                    col.dataIndex=col.scopedSlots.customRender
                    col.scopedSlots=undefined
                }
            }
        },
        computed:{
            keyValidate(){
                return this.value.key
                    &&/[a-zA-Z][a-zA-Z_\d]*/
                    .test(this.value.key)?
